import React from "react";

class Covid extends React.Component {
  constructor() {
    super();
    this.state = {
      usData: "",
      stateData: [],
    };
  }

  componentDidMount() {
    fetch("https://covidtracking.com/api/v1/us/current.json")
      .then((resp) => resp.json())
      .then((usResp) => {
        this.setState({ usData: usResp[0] });
      });

    fetch("https://covidtracking.com/api/v1/states/current.json")
      .then((resp) => resp.json())
      .then((findResp) => {
        this.setState({ stateData: findResp });
      });
  }

  render() {
    const { usData, stateData } = this.state;
    return (
      <div class="container">
        <h1>Coronavirus in the United States</h1>
        <p>Last synced: {usData.lastModified}</p>

        <br />
        <table class="table">
          <tbody>
            <tr>
              <th>Positive Cases</th>
              <th>Negative Cases</th>
              <th>Deaths</th>
              <th>Total tested</th>
            </tr>
            <tr>
              <td>{usData.positive}</td>
              <td>{usData.negative}</td>
              <td>{usData.death}</td>
              <td>{usData.totalTestResults}</td>
            </tr>
          </tbody>
        </table>
        <br />
        <h3>Coronavirus tracker by state</h3>
        <table class="table table-striped table-sm">
          <tbody>
            <tr>
              <th>State</th>
              <th>Positive Cases</th>
              <th>Negative Cases</th>
              <th>Deaths</th>
              <th>Total tested</th>
            </tr>
            {stateData.map((item) => (
              <tr key={item.fips}>
                <td>{item.state}</td>
                <td>{item.positive}</td>
                <td>{item.negative}</td>
                <td>{item.death}</td>
                <td>{item.totalTestResults}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
}

export default Covid;
